<template>
    <div class="Mine">
        <Header>
            <i slot="first" class="iconfont icon-prev" @click="$router.go(-1)"></i>
            <p slot="mid">我的</p>
            <div slot="last"></div>
        </Header>
        <!-- 登录/注册 -->
        <div class="Mine_login" v-if="this.$store.state.ma==''">
                <div class="tu"><img src="../../images/img/aaa_01.png" alt=""></div>
                <div class="zheng" @click="tiao()">
                    <span>登录/注册</span>
                    <div class="phone">
                        <div class="iconfont icon-shouji"></div>
                        <div class="binding">暂无绑定手机号</div>
                    </div>
                </div>
                <div class="Rightarrow">
                    <router-link to="/login" >
                        <van-icon name="arrow" /> 
                     </router-link>
                </div>
        </div>
        <!-- 登录成功后的页面 -->
         <div class="Mine_login" v-if="this.$store.state.ma!=''">
                <div class="tu"><img :src="'//elm.cangdu.org/img/'+this.$store.state.img" /></div>
                <div class="zheng" @click="tiao_zhuan()">
                    <span>{{this.$store.state.ma.username}}</span>
                    <div class="phone">
                        <van-icon name="phone-o" />
                        <div class="binding">暂无绑定手机号</div>
                    </div>
                </div>
                <div class="Rightarrow">
                    <router-link to="/home/account" >
                        <van-icon name="arrow" /> 
                     </router-link>
                </div>
        </div>
        <!-- 余额  优惠  积分 -->
        <div class="integral">
            <ul>
                <li>
                    <p><span style="color:#f90;">0.00</span><span>元</span></p>
                    <p>我的余额</p>
                </li>
                 <li>
                    <p><span style="color:#ff5f3e;">0</span><span>个</span></p>
                    <p>我的优惠</p>
                </li>
                 <li>
                    <p><span style="color:#6ac20b;">0</span><span>分</span></p>
                    <p>我的积分</p>
                </li>
            </ul>
        </div>

        <div class="Mine_list">
            <div class="Mine_list_one">
                <div class="iconfont icon--"></div>
                <div class="list">
                    <router-link to="/home/orderlist" tag="div">
                        <span>我的订单</span>
                        <div class="tb2"><van-icon name="arrow" /></div>
                    </router-link>
                </div>
            </div>
            <div class="Mine_list_one">
                <div class="iconfont icon-shangchengjifenshangcheng_xuanzhong"></div>
                <div class="list">
                    <router-link to="/home/orderlist" tag="div">
                        <span>积分商城</span>
                        <div class="tb2"><van-icon name="arrow" /></div>
                    </router-link>
                </div>
            </div>
            <div class="Mine_list_one">
                <div class="iconfont icon-huangguan"></div>
                <div class="list">
                    <router-link to="/home/orderlist" tag="div">
                        <span>饿了么会员卡</span>
                        <div class="tb2"><van-icon name="arrow" /></div>
                    </router-link>
                </div>
            </div>
        </div>
        <div class="Mine_list">
            <div  class="Mine_list_one">
                <div class="iconfont icon-fuwuzhongxin"></div>
                    <div class="list">
                        <router-link to="/home/orderlist" tag="div">
                            <span>服务中心</span>
                            <div class="tb2"><van-icon name="arrow" /></div>
                        </router-link>
                    </div>
            </div>
            <div  class="Mine_list_one">
                    <div class="iconfont icon-eliaomo1"></div>
                    <div class="list">
                        <router-link to="/home/orderlist" tag="div">
                            <span>下载饿了么App</span>
                            <div class="tb2"><van-icon name="arrow" /></div>
                        </router-link>
                    </div>
            </div>
            
        </div> 
    <Footer></Footer>
    </div>
</template>
<script>
import Header from "@/page/City/Header"
import Footer from "@/page/Footer/Footer"
export default {
    components:{
        Header,
        Footer
    },
   data() {
      return {

      }
   },
   created() {
   },
   methods: {
       tiao(){
           this.$router.push('/Login')
       },
       tiao_zhuan(){
           this.$router.push('/home/account')
       }
   }
}
</script>

<style lang="scss">
body{
    background: rgb(243, 239, 239);
}
.Mine{
    width: 100%;
    .integral{
        width: 100%;
        background: white;
        ul{
            width: 100%;
            display: flex;
            padding: 0.2rem 0.15rem;
            li{
                flex: 1;
                text-align: center;
                font-size: 0.4rem;
                >p:nth-of-type(1){
                    span:nth-of-type(1){
                        font-weight: bold;
                    }
                    span:nth-of-type(2){
                        font-size: 0.3rem;
                    }
                }
                >p:nth-of-type(2){
                    font-size: 0.25rem;
                }
            }
        }
    }
    .Mine_list{
        width: 100%;
        background: white;
        margin-top: 0.2rem;
        .Mine_list_one{
           width: 100%;
           display: flex;
            .icon-shangchengjifenshangcheng_xuanzhong{
               font-size: 0.35rem;
               margin: 0.3rem 0.2rem;
               color: #EC6718;
            }
            .icon--{
                font-size: 0.35rem;
                margin: 0.3rem 0.2rem;
            }
            .icon-huangguan{
                font-size: 0.35rem;
                margin: 0.3rem 0.2rem;
                color: #FFC636;
            }
            .icon-fuwuzhongxin{
                font-size: 0.35rem;
                margin: 0.3rem 0.2rem;
                color: #4AA5F0;
            }
            .icon-eliaomo1{
                font-size: 0.35rem;
                margin: 0.3rem 0.2rem;
                color: #4AA5F0;
            }
            .list{
                width: 100%;
                display: flex;
                margin-left: 0.25rem;
                border-bottom: 1px solid rgb(235, 231, 231);
                line-height: 0.65rem;
                span{
                    font-size: 0.3rem;
                }
                .tb2{
                    font-size: 0.3rem;
                    margin-left: 6rem;
                    margin-top: -0.6rem;
                    color: gray;
                }
            }
        }
    }
    .Mine_login{
        background: #2F90E8;
        display: flex;
        margin-top:0.75rem;
        .tu{
            img{
                width: 1.5rem;
                height: 1.5rem;
                border-radius: 0.8rem;
                margin: 0.5rem 0.25rem;
            }
        }
        .zheng{
            flex: 1;
            span{
                font-size: 0.3rem;
                color: white;
            }
            .phone{
                color: white;
                display: flex;
                .van-icon{
                    font-size: 0.5rem;
                }
            }
            .binding{
                font-size: 0.3rem;
                color: white;                
            }
        }
        .Rightarrow{
            a{
                font-size: 0.35rem;
                color: white;
            }
        }
    }
}


</style>


